import State from '../state/state'
import initGL from './Init'
import animation from './Animation'
import { outInterior, closeDoor, openLFDoor } from './Animation.Action'
import carConfig from '../config/carConfig'
import { getAssetPath, Ftween, Hex2RGB } from './Tools' 
import { Scene, Camera, Renderer } from './Three.Constant.js'
import Loaders from './Loaders'
import getMaterial from './materials/GetMaterial'
import loadAssets from './LoadAssets'
import { getUsrData } from './Tools'
import Postprocessing from './Postprocessing'
import { setPileMaterial, switchLightTexture } from './LoadAssets.AfterModel'
import { hideShade } from './shade'
import texConfig from '../config/texConfig'
import { doors, showSize } from './actions'


const fontregl = {

  initGL,

  animation,

  hideShade,

  showSize,

  showModel: async (dom, name) => {

    initGL(dom)
    animation()

    // State.composer = Postprocessing[State.postprocessing]()  // 

    if ( State.loadedCar === name && State.sceneCache.length > 0 ) {
      // 
      Scene.children = State.sceneCache
    } else {
      await loadAssets(await getUsrData(name))
    }

  },


  changeCarShaderColor: async ( name ) => {

    let path = `${getAssetPath()}${name}`, data;
    
    if (process && process.env && process.env.NODE_ENV === 'development') {
      data = await Loaders['json'](path)
    } else {
      data = await Loaders['ftrn'](path)
    }

    data.forEach( async material => {
      let mesh = Scene.getObjectByName(material.name)
      if (mesh) {
        let { value } = mesh.material.uniforms.paintColor1
        await Ftween(value, Hex2RGB(material.color1), 200 )
      }
    })
  },


  changeMaterials: async name => {

    if ( !State.carCode ) {
      throw Error('carCode not found! (Function: changeMaterials)')
    }
  
    let color;
    for (const item of carConfig[State.carCode].interiorColors) {
      if (item.colorCode === name) {
        color = item.data
      }
    }
  
    if (!color) {
      return console.warn('InteriorColor is not found')
    }
  
    let path = `${getAssetPath()}${color}`, data;
    
    if (process && process.env && process.env.NODE_ENV === 'development') {
      data = await Loaders['json'](path)
    } else {
      data = await Loaders['ftrn'](path)
    }
  
    for ( let item of data ) {
      let material = await getMaterial(item),
        mesh = Scene.getObjectByName(item.name)
      if ( mesh ) {
        mesh.material = material
      }
    }
  },

  async openLFDoor() {
    return await openLFDoor()
  },


  modelSwitching: async modelName => {
    let data = await getUsrData()
    if ( data.hasOwnProperty('ModelSwitching') ) {

      const models = data.ModelSwitching[modelName]

      if (!models) {
        throw Error(`${modelName} is not found!(Function: fontregl.modelSwitching)`)
      }

      for ( let item of models ) {
        let mesh = Scene.getObjectByName(item.name)
        if ( mesh ) { 
          if ( item.type ) {
            let material = await getMaterial(item)
            mesh.material= material
          } else {
            mesh.material.visible = item.visible
          }
        }
      }
    }
  },


  switchWheelAnimation (bool, speed) {
    
    State.isRotateWheel = bool

    State.rotateWheelSpeed = speed

    for (let key of Object.keys(State.spriteState)) {
      let sprite = Scene.getObjectByName(key)
      sprite.visible = !State.isRotateWheel
    }

    if ( State.isRotateWheel ) {
      doors.closeAllDoor()
    } 
  },

  cancelAnimation () {
    if ( typeof State.animateId === 'number' ) {
      cancelAnimationFrame(State.animateId)
      State.animateId = null
    }
  },


  resize ( w, h ) {
    Camera.aspect = w / h
    Camera.updateProjectionMatrix()
		Renderer.setSize(w, h)
  },


  dispose () {
    // cancel animation
    this.cancelAnimation() 

    // freeing memory
    for ( let i = 0; i < Scene.children.length; i++ ) {
      console.log('remove')
      if (Scene.children[i].material) Scene.children[i].material.dispose() 
      if (Scene.children[i].geometry) Scene.children[i].geometry.dispose()
      Scene.remove(Scene.children[i])
      i--
    }
    console.log(Scene)


    // texture
    for (let i in State.textureCache) {
      State.textureCache[i].dispose()
      delete State.textureCache[i]
    }

    // del hotspot
    for (let k in State.hotSpotState) {
      delete State.hotSpotState[k]
    }
  },


  changeView (name) {
    this.modelSwitching(name)
  },


  async openLights (bool) {
    // 

    let { SpriteLights } = await getUsrData()

    State.isOpenLight = bool

    // sprite
    for(let i=0, len=SpriteLights.length; i<len; i++) {
      let leftLight = Scene.getObjectByName(`light_l_${i}`)
      let rightLight = Scene.getObjectByName(`light_r_${i}`)

      leftLight.visible = State.isOpenLight
      rightLight.visible = State.isOpenLight
    }
    
    // 
    let lightGlass = Scene.getObjectByName('12_headlighting_glass.drc')
    lightGlass.visible = !State.isOpenLight

    // switchLightTexture(State.isOpenLight)
  },



  async resetCamera () {
    await closeDoor(true) // 
    await outInterior()
  },

  async showSellPoint (bool) {
    let { Sprites } = await getUsrData()

    State.isShowSellPoints = bool

    let count = 0
    for(let i=0, len=Sprites.length; i<len; i++){
      count++
      if(count <= 2){
        let sprite = Scene.getObjectByName(`spark_tip_0${count}`)
        sprite.visible = State.isShowSellPoints
      }else{
        break
      }
    }
  }

}




export default fontregl


