/**
 *  Car Paint Shader
 */

const vertexShader = `

  uniform float flakeScale;
  uniform float time;

  varying vec4 mvPosition;
  varying vec3 worldNormal;
  varying vec3 cameraToVertex;
  varying vec2 vUv;
  varying vec2 flakeUv;
  varying vec2 shadowCoords;
  varying vec2 texCoords;

  varying vec4 forFragColor;
  varying vec3 vertPos;
  varying vec4 worldPosition;

  void main() {
    mvPosition = modelViewMatrix * vec4( position, 1.0 );
    worldPosition = modelMatrix * vec4( position, 1.0 );
    vertPos = vec3(mvPosition) / mvPosition.w;

    worldNormal = mat3( modelMatrix[ 0 ].xyz, modelMatrix[ 1 ].xyz, modelMatrix[ 2 ].xyz ) * normal;
    cameraToVertex = normalize(worldPosition.xyz - cameraPosition);
    vUv = uv;
    flakeUv = uv * flakeScale;
    shadowCoords = uv;
    texCoords = uv;

    gl_Position = projectionMatrix * mvPosition;
    //if (time > 0.0) gl_Position += vec4(0.0, 3.0*sin(time * 2.0 + worldPosition.x / 20.0) + 3.0*sin(time * 2.0 + worldPosition.z / 20.0), 0.0, 0.0);
  }
`

const fragmentShader = `

uniform vec3 paintColor1;
uniform vec3 paintColor2;
uniform vec3 paintColor3;
uniform float time;

uniform float normalPerturbation;
uniform float microflakePerturbationA;
uniform float microflakePerturbation;
uniform bool filmic;

uniform float glossLevel;
uniform samplerCube envMap;
uniform samplerCube staticSkybox;

uniform sampler2D normalMap;
uniform sampler2D microflakeNMap;
uniform vec3 flakeColor;
uniform float normalScale;
uniform sampler2D map;

varying vec2 vUv;
varying vec2 flakeUv;
varying vec2 shadowCoords;
varying vec2 texCoords;

varying vec3 worldNormal;
varying vec4 mvPosition;
varying vec3 cameraToVertex;
varying vec3 vertPos;
varying vec4 worldPosition;

const vec3 lightPos = vec3(0.0, 200.0, 700.0);
const vec3 lightPos2 = vec3(700.0, 200.0, 0.0);
const vec3 lightPos3 = vec3(0.0, 200.0, -700.0);
const vec3 lightPos4 = vec3(-800.0, 200.0, 0.0);

const vec3 specColor = vec3(1.0, 1.0, 1.0);


// This function taken directly from the three.js phong fragment shader.
// http://hacksoflife.blogspot.ch/2009/11/per-pixel-tangent-space-normal-mapping.html
vec3 perturbNormal2Arb( vec3 eye_pos, vec3 surf_norm ) {

  vec3 q0 = dFdx( eye_pos.xyz );
  vec3 q1 = dFdy( eye_pos.xyz );
  vec2 st0 = dFdx( vUv.st );
  vec2 st1 = dFdy( vUv.st );

  vec3 S = normalize( q0 * st1.t - q1 * st0.t );
  vec3 T = normalize( -q0 * st1.s + q1 * st0.s );
  vec3 N = normalize( surf_norm );

  vec3 mapN = texture2D( normalMap, vUv ).xyz * 2.0 - 1.0;
  mapN.xy = normalScale * mapN.xy;
  mat3 tsn = mat3( S, T, N );
  return surf_norm;
  return normalize( tsn * mapN );
}

vec3 perturbSparkleNormal2Arb( vec3 eye_pos, vec3 surf_norm ) {

  vec3 q0 = dFdx( eye_pos.xyz );
  vec3 q1 = dFdy( eye_pos.xyz );
  vec2 st0 = dFdx( vUv.st );
  vec2 st1 = dFdy( vUv.st );

  vec3 S = normalize( q0 * st1.t - q1 * st0.t );
  vec3 T = normalize( -q0 * st1.s + q1 * st0.s );
  vec3 N = normalize( surf_norm );

  vec3 mapN = texture2D( microflakeNMap, flakeUv ).xyz * 2.0 - 1.0;
  mapN.xy = 1.0 * mapN.xy;
  mat3 tsn = mat3( S, T, N );
  return normalize( tsn * mapN );
}

vec3 intersectCubeMap(vec3 rayOrigin, vec3 rayDir, float cubeSize){

  vec3 t = (cubeSize * sign(rayDir) - rayOrigin) / rayDir;
  float minT = min(min(t.x, t.y), t.z);
  return rayOrigin + minT * rayDir;
}

vec3 getSpecular(vec3 irradiance, vec3 worldDir, vec3 viewDir, vec3 fleckedNormal, float roughnessSqr, float fFresnel){
  vec3 halfVec = normalize(worldDir + viewDir);
  float micro = max(dot(halfVec, fleckedNormal), 0.0);
  float denom = (micro * micro) * (roughnessSqr/2.0 - 1.0) + 1.0;
  float distr = roughnessSqr/2.0 / (denom * denom);
  return distr * fFresnel * irradiance;
}

void main() {

  float roughness = .04;
  float roughnessSqr = roughness * roughness;

  // radeon normal

  vec3 normal = perturbNormal2Arb( mvPosition.xyz, worldNormal );


  //vec3 normal = normalize(worldNormal); // V2

  // V2 normal
  /*vec3 S = normalize(worldBitangent);
  vec3 T = normalize(worldTangent);*/
  /*
  vec3 q0 = dFdx( mvPosition.xyz );
  vec3 q1 = dFdy( mvPosition.xyz );
  vec2 st0 = dFdx( vUv.st );
  vec2 st1 = dFdy( vUv.st );
  vec3 S = normalize( q0 * st1.t - q1 * st0.t );
  vec3 T = normalize( -q0 * st1.s + q1 * st0.s );

  vec3 N = normalize(worldNormal);

  mat3 tsn = mat3( T, S, N );

  vec4 normalGloss = texture2D( normalMap, texCoords );
  normalGloss.xyz = (normalGloss.xyz - 128.0 / 255.0) * 2.0;
  vec3 normal = normalize(tsn * normalGloss.xyz);
*/

/*vec4 fleck = texture2D(flecksSampler, fleckCoords);
vec3 fleckedNormal = normalGloss.xyz;
fleckedNormal.xy += (fleck.xy - .5) * fleckScale;
fleck.z *= fleckScale;
fleckedNormal = tsn * normalize(fleckedNormal);
roughnessSqr -= fleck.z * .1;
if (roughnessSqr < 0.0) roughnessSqr = 0.0;*/
vec3 fleckedNormal = normal;  // V2
// end V2 normal

vec3 color = vec3(0.0);

// radeon fresnel calc.
//float fFresnel = dot( normalize( -cameraToVertex ), normal );
// end radon

// V2 fresnel calc.
//angle to the power of 5
vec3 viewDir = normalize( -cameraToVertex );
float angle = 1.0 - dot( viewDir, normal);
float fFresnel = angle * angle;
fFresnel *= fFresnel;
//fFresnel *= angle;

float normalSpecularReflection = .02;
fFresnel = normalSpecularReflection + (1.0 - normalSpecularReflection) * fFresnel;
// TODO: V2 fresnel is a bit flatter. Possible cause - camera FOV.
// end V2 fresnel calc.

// radeon reflection
/*
vec3 reflection = 2.0 * worldNormal * fFresnel - normalize(-cameraToVertex);
vec4 envColor = textureCube( envMap, vec3( -reflection.x, reflection.yz ), 1.0 );
vec3 rc = envColor.rgb;
*/
// end radeon reflection.

// V2 reflection
float skyboxSize = 50000.0;
vec3 skyboxPosition = vec3(0.0);  
vec3 reflectedView = -reflect(viewDir, normal);
vec3 offsetRefl = intersectCubeMap(worldPosition.xyz - skyboxPosition, reflectedView, skyboxSize);
vec3 rc0 = textureCube(envMap, offsetRefl).xyz; 

//vec3 staticReflection = textureCube(staticSkybox, reflectedView).xyz;
//        rc = mix(staticReflection, rc, fresnelFactor);
//        vec3 specular = textureCube(specularProbe, reflectedView).xyz;
vec3 rc = rc0 * rc0 ;
#ifdef NORMAL_MAP
    rc *= (3.0 - fleck.z * 10.0);
#else
    rc *= 3.0;
#endif

rc *= fFresnel;
// end V2 reflection.

// Lighting

// move light depending on cam?
//vec3 lightPos = -mvPosition.xyz;
//lightPos.y = -lightPos.y;

// radeon lighting
/*
vec3 lightDir = normalize(lightPos - vertPos);
// directional light?
//vec3 lightDir = normalize(vec3(0.0,1.0,-5.0));
vec3 reflectDir = reflect(-lightDir, worldNormal);
// lambertian currently unused. only specular hilights are used.
float lambertian = max(dot(lightDir,normal), 0.0);
float specular = 0.0;
if(lambertian > 0.0) {
  float specAngle = max(dot(reflectDir, -cameraToVertex), 0.0);
  specular = pow(specAngle, 512.0);
}
*/

vec3 diffuseLight = vec3(0.0);
vec3 specular = vec3(0.0);

vec3 worldDir = normalize(lightPos - worldPosition.xyz);
float lambert = max(dot(worldDir, fleckedNormal), 0.0);
vec3 irradiance = vec3(1.0,0.8,0.6) * lambert;
diffuseLight += irradiance;
specular += getSpecular(irradiance, worldDir, viewDir, fleckedNormal, roughnessSqr, fFresnel)*0.05;

worldDir = normalize(lightPos2 - worldPosition.xyz);
lambert = max(dot(worldDir, fleckedNormal), 0.0);
irradiance = vec3(1.0,0.8,0.6) * lambert;
diffuseLight += irradiance;
specular += getSpecular(irradiance, worldDir, viewDir, fleckedNormal, roughnessSqr, fFresnel)*0.05;

worldDir = normalize(lightPos3 - worldPosition.xyz);
lambert = max(dot(worldDir, fleckedNormal), 0.0);
irradiance = vec3(1.0,0.8,0.6) * lambert;
diffuseLight += irradiance;
specular += getSpecular(irradiance, worldDir, viewDir, fleckedNormal, roughnessSqr, fFresnel)*0.05;

worldDir = normalize(lightPos4 - worldPosition.xyz);
lambert = max(dot(worldDir, fleckedNormal), 0.0);
irradiance = vec3(1.0,0.8,0.6) * lambert;
diffuseLight += irradiance;
specular += getSpecular(irradiance, worldDir, viewDir, fleckedNormal, roughnessSqr, fFresnel)*0.05;  

// radeon paint.
/*
vec3 vFlakesNormal = perturbSparkleNormal2Arb(mvPosition.xyz, worldNormal);
vec3 vNp1 = microflakePerturbationA * vFlakesNormal + normalPerturbation * worldNormal;
vec3 vNp2 = microflakePerturbation * ( vFlakesNormal + worldNormal ) ;

float fFresnel1 = clamp(dot( -cameraToVertex, vNp1 ), 0.0, 1.0);
float fFresnel2 = clamp(dot( -cameraToVertex, vNp2 ), 0.0, 1.0);

float fFresnel1Sq = fFresnel1 * fFresnel1;
vec3 paintColor = 0.3 * fFresnel1 * paintColor1 +
                   0.35 * fFresnel1Sq * paintColor2 +
                   0.35 * fFresnel1Sq * fFresnel1Sq * paintColor3 +
                   pow( fFresnel2, 16.0 ) * flakeColor;
                   */
// end radeon paint.

// V2 paint
float ratio = angle * angle * angle;
vec3 paintColor = paintColor1; //mix(paintColor1, paintColor2, ratio);
paintColor = mix(paintColor1, paintColor2, ratio);
// end V2 paint


// debug fresnel components.
//vec3 paintColor = pow( fFresnel2, 16.0 ) * flakeColor;
//vec3 paintColor = fFresnel1Sq * fFresnel1Sq * paintColor3;
//vec3 paintColor = fFresnel1Sq * paintColor2;
//vec3 paintColor = fFresnel1 * paintColor1;

// radeon compositing.
/*
color += paintColor * (0.1 + (0.9 * fFresnel)); 
// apply reflection
color += rc * (1.0 - 0.9 * fFresnel);
*/

// apply texture
vec4 colorSample = texture2D(map, texCoords);
diffuseLight *= colorSample.xyz;

rc *= colorSample.xyz;
// clamp the reflections based on AO map.
if (colorSample.x < 0.5) {
  //rc = vec3(0.0);
}

// V2 compositing.

//color = paintColor;
color += paintColor * colorSample.xyz   + specular;
//color+= paintColor * diffuseLight  ;
color +=  rc*0.85;



// filmic tone mapping.
if (filmic) {
  vec3 film = max(vec3(0.0), color - 0.005);
  color = (film*(7.2*film+.7))/(film*(6.2*film+1.7)+0.06);
}

if(gl_FrontFacing)
  gl_FragColor = vec4(color, 1.0);
else
  gl_FragColor = vec4(0.0, 0.0, 0.0, 1.0);
 }
`





export { vertexShader, fragmentShader }



