import addLights from './LoadAssets.Lights.js'
import addSprites from './LoadAssets.Sprites'
import addModels from './LoadAssets.Models.js'
import {
  cameraAnimation,
  doorAnimation
} from './Animation.Action'
import State from '../state/state'
import { Scene } from './Three.Constant.js';



const loadAssets = async usrData => {

  // light
  await addLights(usrData.Lights)


  // model
  console.time('load model')
  await addModels(usrData)   
  console.timeEnd('load model')

    // sprite
  await addSprites(usrData.Sprites)
  
}




export default loadAssets


