import { Scene, TextureLoader } from './Three.Constant'
import { getAssetPath, getUsrData } from './Tools' 
import State from '../state/state'
import getCarPaintShader from './materials/CarPaintShader'
import texConfig from '../config/texConfig'

const THREE  = require('three')

/**
 * 
 */
export async function  addSpriteCarLight (){
  let { SpriteLights } = await getUsrData()

  let lightArr = SpriteLights

  // let count = 0
  for(let i=0, len=lightArr.length; i<len; i++){
    let obj = lightArr[i]
    let material = new THREE.SpriteMaterial({ 
      transparent: true,
      blending: THREE.AdditiveBlending,
      needsUpdate: true,
    })

    let sprite = new THREE.Sprite( material )
    let spriteRight = new THREE.Sprite( material )
    if(obj.type === 'front'){
      // test
      // let size = 38
      let size = obj.size
      let texture = TextureLoader.load(`${getAssetPath()}textures/${obj.imgPath}`)
      sprite.material.map = texture
      sprite.scale.set(size, size, size)
      sprite.position.set(obj.pos[0], obj.pos[2] - 81, -obj.pos[1])
      
      spriteRight.material.map = texture
      spriteRight.scale.set(size, size, size)
      spriteRight.position.set(obj.pos[0], obj.pos[2] - 81, -(-obj.pos[1]))
    }
    if(obj.type === 'back'){
      // test
      // let size = 18
      let size = obj.size
      let texture = TextureLoader.load(`${getAssetPath()}textures/${obj.imgPath}`)
      sprite.material.map = texture
      sprite.scale.set(size, size, size)
      sprite.position.set(obj.pos[0], obj.pos[2] - 80, -obj.pos[1])
      
      spriteRight.material.map = texture
      spriteRight.scale.set(size, size, size)
      spriteRight.position.set(obj.pos[0], obj.pos[2] - 80, -(-obj.pos[1]))
    }

    sprite.name = `light_l_${i}`
    spriteRight.name = `light_r_${i}`
    sprite.visible = false
    spriteRight.visible = false

    if(obj.follow){
      // 
      if(obj.follow === '38_trunk_light_map_c.drc'){
        let mesh = Scene.getObjectByName('38_trunk_light_map_c.drc')
        mesh.add(sprite)
        mesh.add(spriteRight)
      }
    }else{
      Scene.add(sprite)
      Scene.add(spriteRight)
    }

  }
}

/**
 * 
 * @param {*} bool 
 */
export async function switchLightTexture (bool){
  let lightPath = 'headlight_map_c.png'
  let noLightPath = 'headlight_map_c.jpg'
  let texBackLight = TextureLoader.load(`${getAssetPath()}textures/${lightPath}`)
  let texNoBackLight = TextureLoader.load(`${getAssetPath()}textures/${noLightPath}`)

  // let { Materials } = await getUsrData()

  let arr = ['61_lighting_map_c.drc', '38_trunk_light_map_c.drc']

  if(bool){
    for(let obj of arr){
      let mesh = Scene.getObjectByName(obj)
      mesh.material.map = texBackLight
      mesh.material.needsUpdate = true
    }
  }else{
    for(let obj of arr){
      let mesh = Scene.getObjectByName(obj)
      mesh.material.map = texNoBackLight
      mesh.material.needsUpdate = true
    }
  }
}


// 
export async function loadFloor () {

  let { Floor } = await getUsrData()
  let { FogSky } = await getUsrData()

  // 
  if (Floor) {

    let { x, y } = Floor.repeat,
      { width, height} = Floor.size;
    // texture
    let texture = new THREE.TextureLoader().load( `${window.FONTRE_PATH}/project/${State.carCode}/assets/textures/${Floor.name}` );
    // let normal = new THREE.TextureLoader().load( `${window.FONTRE_PATH}/project/${State.carCode}/assets/textures/${Floor.normal}` );
    texture.wrapS = texture.wrapT = THREE.RepeatWrapping;
    texture.repeat.set( x, y )

    // geometry
    let geo = new THREE.PlaneGeometry(width, height)

    let material = new THREE.MeshStandardMaterial({
      color: Floor.color,
      map: texture,
      // normalMap: normal,
      roughness: 1,
      side: THREE.DoubleSide
    })

    let plane = new THREE.Mesh(geo, material)
    plane.name = Floor.name
    plane.rotateX(Math.PI/2)
    plane.position.set(0, -81, 0)

    Scene.add(plane)

    // let skybox = Scene.getObjectByName('scene_02_map_c.drc')
    // skybox.material.fog = false
    // skybox.material.needsUpdate = true
    // skybox.position.set(0, 50, 0)

  }
}




