

import { Orbit, Renderer, Scene, Camera, FPS, Helper, Clock} from './Three.Constant'
import { spriteAnimation, toRotateWheel, animationFbx } from './Animation.Action.js'
import State from '../state/state'

const THREE = require('three')
const TWEEN = require('@tweenjs/tween.js')

const animation = function () {

  if (typeof State.animateId === 'number' ) return 

  let count = 0;
  let wheelCount = 0
  console.time('animation');
  (function _animate() {
    count++
    
    if ( State.isRotateWheel ) {
      wheelCount++
      toRotateWheel(wheelCount, State.rotateWheelSpeed)  // 
    } 

    spriteAnimation(count)
    
    if ( State.composer ) {
      State.composer.render()
    } else {
      Renderer.render(Scene, Camera)
    }

    // console.log(Camera.position)

    TWEEN.update()
    Orbit.update()

    if ( FPS ) FPS.update()

    State.animateId = requestAnimationFrame(_animate)

  }())
}



export default animation


