import { Mouse, Raycaster, Scene, Camera } from './Three.Constant.js'
import { getUsrData, Ftween } from './Tools'
import State from '../state/state'
import { toInterior, outInterior } from './Animation.Action.js'

const THREE = require('three')
const TWEEN = require('@tweenjs/tween.js'),
  OUT = TWEEN.Easing.Circular.Out,
  IN = TWEEN.Easing.Circular.In;

let lock = null




let actions = {

  // 
  async openSunroof (data) {

    if (lock ) return 

    // 
    let spriteMesh = Scene.getObjectByName('00_sunroof_tip')
    // spriteMesh.visible = !State.hotSpotState[data.name]

    let radian = Math.PI / 180 * 6
    lock = true


    for (let item of data.models) {
      let mesh = Scene.getObjectByName(item.name)

      if ( State.hotSpotState[data.name]) {
        await Ftween(mesh.position, { y: mesh.position.y + 1 }, 300 )
        Ftween(mesh.rotation, { z: -radian }, 1500)
        Ftween(spriteMesh.position, { x: mesh.position.x + 40 }, 1500 )
        await Ftween(mesh.position, { x: mesh.position.x + 40 }, 1500 )

      } else {
        Ftween(mesh.rotation, { z: 0 }, 1500)
        Ftween(spriteMesh.position, { x: mesh.position.x - 40 }, 1500 )
        await Ftween(mesh.position, {x: mesh.position.x - 40 }, 1500)
        await Ftween(mesh.position, { y: mesh.position.y - 1 }, 300 )
      }
    }
    lock = false
  }
}


export const bindEvent = function (dom, e) {

  e.preventDefault()
  let x = e.offsetX / dom.clientWidth * 2 - 1, 
      y = -(e.offsetY / dom.clientHeight) * 2 + 1

  Mouse.set(x, y)

  Raycaster.setFromCamera(Mouse, Camera)
  let intersects = Raycaster.intersectObjects(Scene.children, true)

  if (intersects.length > 0) {
    for (let i = 0; i < intersects.length; i++) {
      onClickMesh(intersects[i].object)
      return 
    }
  }
}


export async function onClickMesh(mesh) {
  console.log(mesh.name)

  if (State.isRotateWheel) return 

  clickSellPoint(mesh.name)

  clickToInteriorPoint(mesh.name)
  
  let { Hotspot } = await getUsrData()
  if ( Hotspot && Hotspot.length > 0 ) {
    for (const hot of Hotspot) {
      if (Array.isArray(hot.hotspot)) {
        for ( const name of hot.hotspot ) {
          if ( name === mesh.name ) {
            openDoor(hot)
          }
        }
      } else {
        throw Error ('HotSpot hotspots are not Array')
      }
    }
  }
}

/**
 * 
 * @param {String} name 
 */
const clickSellPoint = (name) => {
  if(name.indexOf('spark_tip_') >= 0){
    window.FontreGL.activeSellPoint(name)
  }
}

/**
 * 
 */
const clickToInteriorPoint = (name) => {
  if(name === 'to_interior_tip'){
    window.FontreGL.activeToInteriorPoint()
  }
}

async function openDoor ( data ) {

  if (lock) return

  let { models, name, electricDoor } = data, callBack;

  State.hotSpotState[name] = !State.hotSpotState[name] 
  console.log(name)
  // 
  if (name === 'open_sunroof') {
    return await actions[data.function](data)
  }

  //
  if ( data.blackLine ) {
    for (const item of data.blackLine) {
      Scene.getObjectByName(item).visible = !State.hotSpotState[name]
    }
  }


  let tweenAll = []
  lock = true
  models.forEach( item => {
    let mesh = Scene.getObjectByName(item.name)

    if (!mesh) return 
    
    if ( State.spriteState[item.name] instanceof THREE.Sprite ) {
      State.spriteState[item.name].visible = !State.hotSpotState[name]
    }
    
    let [ x, y, z ] = State.hotSpotState[name] ? item.targetAngle.map( i => - Math.PI * i / 180 ) : [ 0, 0, 0]

    if ( !State.hotSpotState[name] && electricDoor ) {
      [x , y, z] = item.targetAngle.map( i => -Math.PI * (i / 10) / 180)
      callBack = () => Ftween(mesh.rotation, {x: 0, y:0, z: 0}, 300, IN)
    }
    
    tweenAll.push(Ftween(mesh.rotation, {x, y, z}, 800, OUT, callBack))
  })

  await Promise.all(tweenAll)
  
 

  lock = false  
}













