import { Scene } from '../../Three.Constant'

const THREE = require('three')
const FontLoader = new THREE.FontLoader();



export const length = function (text) {

  //  line 
  var geometry = new THREE.PlaneBufferGeometry( 500, 2 );
  var material = new THREE.MeshBasicMaterial( {color: 0xffffff, side: THREE.DoubleSide} );
  var length  = new THREE.Mesh( geometry, material );

  length.position.set(0, -80, 100)
  length.rotateX( -Math.PI / 2 )
  length.name = 'carLength'

  Scene.add(length)

  // text
  FontLoader.load( `${window.FONTRE_PATH}/font/font.json`,  ( font ) => {

    var shapes = font.generateShapes( text, 18 );
    var geometry = new THREE.ShapeBufferGeometry( shapes );
    geometry.computeBoundingBox();

    let mesh = new THREE.Mesh(geometry, material )
    mesh.position.set(0, -79, 120)
    mesh.rotateX( -Math.PI / 2 )
    mesh.name = 'carLengthText'

    Scene.add(mesh)
  })
}




export const width = function (text) {

  //  line 
  var geometry = new THREE.PlaneBufferGeometry( 200, 2 );
  var material = new THREE.MeshBasicMaterial( {color: 0xffffff, side: THREE.DoubleSide} );
  var length  = new THREE.Mesh( geometry, material );

  length.position.set(-250, -80, 0)
  length.rotateX(-Math.PI/2)
  length.rotateZ(-Math.PI/2)
  length.name = 'carWidth'

  Scene.add(length)

  // text
  FontLoader.load( `${window.FONTRE_PATH}/font/font.json`,  ( font ) => {

    var shapes = font.generateShapes( text, 18 );
    var geometry = new THREE.ShapeBufferGeometry( shapes );
    geometry.computeBoundingBox();

    let mesh = new THREE.Mesh(geometry, material )
    mesh.position.set(-270, -79, -40)
    mesh.rotateX( -Math.PI / 2 )
    mesh.rotateZ(-Math.PI/2)
    mesh.name = 'carWidthText'

    Scene.add(mesh)
  })
}




export const height = function (text) {

  //  line 
  var geometry = new THREE.PlaneBufferGeometry( 300, 2 );
  var material = new THREE.MeshBasicMaterial( {color: 0xffffff, side: THREE.DoubleSide} );
  var length  = new THREE.Mesh( geometry, material );

  length.position.set(250, -80, 100)
  length.rotateZ(-Math.PI/2)
  length.rotateX(-Math.PI/2)
  length.name = 'carHeight'


  Scene.add(length)

  // text
  FontLoader.load( `${window.FONTRE_PATH}/font/font.json`,  ( font ) => {

    var shapes = font.generateShapes( text, 18 );
    var geometry = new THREE.ShapeBufferGeometry( shapes );
    geometry.computeBoundingBox();

    let mesh = new THREE.Mesh(geometry, material )
    mesh.position.set(250, 0, 100)
    mesh.rotateY( -Math.PI / 2 )
    mesh.name = 'carHeightText'

    Scene.add(mesh)
  })
}



let sizeNameList = [ 'carLength', 'carLengthText', 'carHeight', 'carHeightText', 'carWidth', 'carWidthText' ]

export const removeSize = function () {

  for (let item of sizeNameList) {

    let mesh = Scene.getObjectByName(item)

    Scene.remove(mesh)
  }
}


