import getMaterial from "./materials/GetMaterial";
import { Scene, Matrix4} from './Three.Constant.js'

import { getAssetPath, getUsrData } from './Tools' 
import State from '../state/state'
import { ENGINE_METHOD_RSA } from "constants";
const THREE = require('three')

const loadAssets = async Sprites => {

  for ( const item of Sprites ) {

    let material = await getMaterial(item.material)
    let Sprite =  new THREE.Sprite(material)
      // { x, y, z } = fontreConfig.getMeshOffset()

    Sprite.name = item.name

    Sprite.position.set(
      item.position.x + 0,
      item.position.y + -81,
      item.position.z + 0
    )
    
    Scene.add(Sprite);
    State.spriteState[item.name] = Sprite
  }

  // await addGroup()
}


