import { Orbit } from './Three.Constant'
import { Ftween } from './Tools'

const TWEEN = require('@tweenjs/tween.js')

const STOP_TIME = 2000, // 
    MOVE_DISTANCE = 280,   // 
    CURRENT_DISTANCE = Orbit.customDistance 

const MouseMoveCallback =  (() => {
  let timer, lock = false

  return () => {
    if ( lock ) return   // 
    if ( timer ) clearTimeout(timer)  // 
  
    setState()
    lock = true
  
    setTimeout(() => { lock = false }, 500)
    timer = setTimeout(() => resetState() , STOP_TIME)
  }
})()


function setState() {
  Orbit.autoRotate = false
  Ftween(Orbit, { customDistance: CURRENT_DISTANCE - MOVE_DISTANCE }, 250, TWEEN.Easing.Quadratic.Out)
}


function resetState () {
  Orbit.autoRotate = true
  Ftween(Orbit, { customDistance: Orbit.customDistance + MOVE_DISTANCE }, 250, TWEEN.Easing.Quadratic.Out)
}



export default MouseMoveCallback


