


const process = {

  processShowCallback: null,
  processHideCallback: null,
  processNumCallback: null,

  processShow () {
    console.log('show')
    if ( this.processShowCallback ) {
      this.processShowCallback()
    }
  },

  processHide () {
    console.log('hide')
    if ( this.processHideCallback ) {
      this.processHideCallback()
    }
  },

  processNum ( num ) {
    if ( this.processNumCallback ) {
      this.processNumCallback(num)
    }
  }

}


export default process




