

export const showShade = (bg = 'black') => {
  
  let shadeEl = document.querySelector('#fontregl_shade')

  if ( !shadeEl ) return console.warn('element [fontregl_shade] not exist ')

  shadeEl.style.width = '100%'
  shadeEl.style.background = bg

  shadeEl.style.opacity = 1
}


