
import './libs/FontreLoader'
import './libs/DRACOLoader'
import './libs/FontreOrbitControls'
import './libs/Lensflare'

import './libs/CopyShader'
import './libs/postprocessing/EffectComposer'
import './libs/postprocessing/SSAARenderPass'
import './libs/postprocessing/TAARenderPass'
import './libs/postprocessing/RenderPass'
import './libs/postprocessing/MaskPass'
import './libs/postprocessing/ShaderPass'

import State from '../state/state'
import Detector from './Detector.js'


const THREE = require('three')

THREE.DRACOLoader.setDecoderPath( window.FONTRE_PATH + '/js/libs/' );
THREE.DRACOLoader.setDecoderConfig( { type: 'js' } );

// fps 
let FPS
if ( State.debug ) {
  const Staus = require('./libs/Stats')
  FPS = new Staus()
  FPS.showPanel(0)
  document.body.appendChild(FPS.dom)
}


// THREE 
const Scene = new THREE.Scene()

Scene.background = new THREE.Color('#000')
// Scene.fog = new THREE.FogExp2(0xF8F8FF, 0.0008)
Scene.fog = new THREE.Fog('#B2B2B2', 400, 1500)


const Camera = new THREE.PerspectiveCamera()

// const Renderer = new THREE.WebGLRenderer({
//   antialias: true, // antialias:true/false
//   precision: "highp", // precision:highp/mediump/lowp
//   alpha: true, // alpha:true/false
//   powerPreference:'low-power', //   high-performance/low-power
//   preserveDrawingBuffer: false, //do not set true
// })

let rderer = new THREE.CanvasRenderer();
if (Detector.webgl) {
  rderer = new THREE.WebGLRenderer({
     antialias: true, // antialias:true/false
     precision: "highp", // precision:highp/mediump/lowp
      alpha: true, // alpha:true/false
      powerPreference:'low-power', //   high-performance/low-power
      preserveDrawingBuffer: false, //do not set true
    })
}
const Renderer = rderer
// const Renderer = new THREE.WebGLRenderer({
//   antialias: true, // antialias:true/false
//   precision: "mediump", // precision:highp/mediump/lowp
//   alpha: true, // alpha:true/false
//   powerPreference:'low-power', //   high-performance/low-power
//   preserveDrawingBuffer: false, //do not set true
// })


const Orbit = new THREE.OrbitControls(Camera, Renderer.domElement, {
  minDistance: 500, 
  maxDistance: 900,  //400
  customDistance: 550,
  minPolarAngle: Math.PI / 4,
  maxPolarAngle: Math.PI / 2
})


const Color = new THREE.Color()
const Mouse = new THREE.Vector2()
const Matrix4 = new THREE.Matrix4()
const Raycaster = new THREE.Raycaster()
const Sprite = new THREE.Sprite()

const Fileloader = new THREE.FileLoader()
const FontreLoader = new THREE.FONTRELoader()
const dracoLoader = new THREE.DRACOLoader();

const TextureLoader = new THREE.TextureLoader()
const CubeLoader = new THREE.CubeTextureLoader()

const Clock = new THREE.Clock();

TextureLoader.setCrossOrigin('anonymous')
CubeLoader.setCrossOrigin('anonymous')

// ----------------  ---------------

// var helper = new THREE.CameraHelper( Camera );
// Scene.add( helper );
// THREE.Cache.enabled = true  // 

export { Scene, Camera, Sprite, Orbit, Mouse, Matrix4, Raycaster, Renderer, Fileloader, FontreLoader, TextureLoader, CubeLoader, Color, FPS, dracoLoader, AnimateObjs, Helper, Clock}


