import carConfig from '../config/carConfig'
import State from '../state/state'
import { loadJSFile } from '../utils'


export default new class {

  constructor () {
    this.krpano = null
    this.carCode = null
  }

  async init (id, carCode, exterColor, interColor) {
    checkArguments(arguments) 
    this.carCode = carCode

    const Pano = carConfig[carCode].pano
  
    if (!window.embedpano) {
      await loadJSFile(carConfig[carCode].pano.jsFile)
    }

    let xml = Pano.xml_file,
        panoEx = Pano.exteriorHotspot[exterColor],
        panoIn = Pano.interiorScreen[interColor]
  
    return await this.showPano(xml, id, panoEx, panoIn)
  }


  // 
  showPano ( xml, id, exteriors, interior ) {

    return new Promise ( res => {
      window.embedpano({
        id:"fontrePano",
        xml: xml,
        target: id,
        html5: "only",
        mobilescale: 1.0,
        passQueryParameters: true,
        vars: { onstart: `set(startscene, ${interior})` },
        onready: krpanoReady
      })
      this.krpano = document.querySelector('#fontrePano')
      function krpanoReady(krpano) {
        console.log("krpanoReady")
        if (exteriors.length > 0) {
          for (let item of exteriors) {
            krpano.call(`set_hotspot_display(${item})`)
          }
        }
        res()
      }
    })
  }


  // 
  changeExterColor (color) {
    if ( !State.carCode ) return console.warn('pano carCode is null')
    const hotspots = carConfig[State.carCode].pano.exteriorHotspot[color]
    if ( this.krpano ) {
      this.krpano.call("set_hotspot_transparent")

      if (hotspots.length > 0 ) {
        for (let item of hotspots) {
          this.krpano.call(`set_hotspot_display(${item})`)
        }
      }
    } 
  }


  // 
  changeInterColor(color) {
    if ( !State.carCode ) return console.warn('pano carCode is null')

    const scene = carConfig[State.carCode].pano.interiorScreen[color]

    if ( !scene ) {
      throw Error(`get pano interiorScreen [${color}] fail! (Function: pano.changeInterColor)`)
    }
    
    if ( this.krpano ) {
      this.krpano.call(`fontre_load_scene(${scene})`)
      // this.krpano.call(color)
      // this.krpano.set("interiorColor", color)
    }
  }


  hideHotSpot (bool) {
    if ( this.krpano ) {
      if(!bool){
        this.krpano.call(`hotspot_display_none`)
      }else{
        this.krpano.call(`hotspot_display_block`)
      }
      
    }
  }

  toggleVR () {
    if ( this.krpano ) {
      this.krpano.call(`toggleVR`)
    }
  }


  // remove pano
  dispose () {
    if ( window.removepano ) {
      this.krpano = null
      window.removepano('fontrePano')
      return
    }
  }

}()




// 
function checkArguments(arr) {
  // if (!document.querySelector(`#${arr[0]}`)) {
  //   throw Error(`element [${arr[0]}] is not exist(Function:initPano)`)
  // }
  if ( !carConfig[arr[1]].pano ) {
    throw Error(`${arr[1]} config [pano] is not exist(Function:initPano)`)
  }

  if ( !carConfig[arr[1]].pano.interiorScreen ||  !carConfig[arr[1]].pano.exteriorHotspot ) {
    throw Error(`${arr[1]} [pano] config is not exist(Function:initPano)`)
  }

  if ( !carConfig[arr[1]].pano.exteriorHotspot[arr[2]] ) {
    throw Error(`${arr[1]} [pano] exteriorHotspot  [${arr[2]}]  is not found! (Function:initPano)`)
  }

  if ( !carConfig[arr[1]].pano.interiorScreen[arr[3]] ) {
    throw Error(`${arr[1]} [pano] interiorScreen [${arr[3]}] is not found! (Function:initPano)`)
  }
}




