  
export default {

   /**
   * 
   * @param  {[type]} objOld [description]
   * @param  {[type]} objNew [description]
   * @return {[type]}        [description]
   */
  diffParamsChangeData (objOld, objNew) {
    let diffObj = {}
    if( objOld && objNew && typeof objOld === 'object' && typeof objNew === 'object') {
      for(let pro in objOld){
        if (objOld.hasOwnProperty(pro) && objNew.hasOwnProperty(pro)) {
          if (objOld[pro] !== objNew[pro]) {
            diffObj[pro] = objNew[pro]
          }
        }
      }
      return diffObj
    } else {
       throw new TypeError('params type must be object', "diffParamsChangeData")
    }
  },

  
  /**
   * 
   * @param  {[type]} param  [description]
   * @param  {[type]} format [description]
   * @return {[type]}        [description]
   */
  validateApiFormat (param, format) {
    if(param !== null) {
      if(param === undefined || typeof param !== format){
        throw new TypeError('property value must be ' + format, "validateApiFormat")
      }
    }
    return true
  } 
}
 


