import setMaterial from './SetMaterial'
import getCarPaintShader from './CarPaintShader'
import { MaterialEditChildren } from  './SetMaterial.Constant'
const THREE = require('three')

const getMaterial = async (parameter) => {
  let material = await getMaterialType(parameter)

  if (material.type !== 'ShaderMaterial') {
    await setMaterial(material, parameter, MaterialEditChildren)
  }
  material.fog = false
  material.name = parameter.name
  material.needsUpdate = true

  return material
}


async function getMaterialType (param) {
  if (param.type === 'CarPaintShader') {
    let material = new THREE.ShaderMaterial(await getCarPaintShader(param))
    material.visible = param.visible
    return material
  } else {
    return new THREE[param.type]()
  }
}


export default getMaterial




