
import setMaterial from './materials/SetMaterial'
import { Scene } from './Three.Constant.js'
// import State from '../state/state'
import { LightEditChildren } from './materials/SetMaterial.Constant'
const THREE = require('three')


const addLights = async Lights => {

  if (!Lights) {
    throw Error(`Lights data are undefined! (Function: addLights)`)
  }

  for (const { type, param } of Lights) {

    let light = getLightType(type)
    await setMaterial(light, param, LightEditChildren)

    Scene.add(light)
  }
}


function getLightType(type) {
  let light, lightParam = {r: 1, g: 1, b: 1}
  switch (type) {
    case 'AmbientLight':
      light = new THREE[type](lightParam, 1)
      break
    case 'HemisphereLight':
      light = new THREE[type](lightParam, lightParam, 1)
      break
    case 'DirectionalLight':
      light = new THREE[type](lightParam, 1)
      break
    case 'PointLight':
      light = new THREE[type](lightParam, 1)
      break
    case 'SpotLight':
      light = new THREE[type](lightParam, 1)
      break
    default:
      throw Error('LightType is error')
  }

  return light
}




export default addLights


