

const Utf8ArrayToStr = array => {
    let len = array.length,
        out = "",
        i = 0,
        char2,
        char3,
        c;

    while (i < len) {
      c = array[i++];
      switch (c >> 4) {
        case 0:
        case 1:
        case 2:
        case 3:
        case 4:
        case 5:
        case 6:
        case 7:
          // 0xxxxxxx
          out += String.fromCharCode(c);
          break;

        case 12:
        case 13:
          // 110x xxxx   10xx xxxx
          char2 = array[i++];
          out += String.fromCharCode(((c & 0x1f) << 6) | (char2 & 0x3f));
          break;

        case 14:
          // 1110 xxxx  10xx xxxx  10xx xxxx
          char2 = array[i++];
          char3 = array[i++];
          out += String.fromCharCode(
            ((c & 0x0f) << 12) | ((char2 & 0x3f) << 6) | ((char3 & 0x3f) << 0)
          );
          break;
        default:
         break
      }
    }
    return out;
  }

const AsciiArrayToStr = array => {
  let out = "",
    i = 0,
    len = array.length;
  let c;
  while (i < len) {
    c = array[i++];
    if (c === 0) {
      break;
    }
    out += String.fromCharCode(c);
  }
  return out;
}

const ArrayToint32 = function (array) {
  return (array[0] & 0xff) | ((array[1] & 0xff) << 8) | ((array[2] & 0xff) << 16) | ((array[3] & 0xff) << 24) 
}



export { Utf8ArrayToStr, AsciiArrayToStr, ArrayToint32}


