
import carConfig from '../config/carConfig'
import Loaders from './Loaders'
import State from '../state/state'
import { TextureLoader, CubeLoader } from './Three.Constant.js'
import { Color } from './Three.Constant'
const THREE = require('three')
const TWEEN = require('@tweenjs/tween.js')


/**
 *  
 * @param {*} carCode 
 */
const getUsrData = (()=> {
  let cache = {} 
  return async () => {
    let { carCode }  = State
    if (!carCode) {
      throw Error('carCode not found! (Function: getUsrData)')
    } 

    if (cache[carCode]) return cache[carCode]

    let path = `${carConfig[carCode].assets}/${carCode}`
  
    let data;
    if (process && process.env && process.env.NODE_ENV === 'development') {
      data = await Loaders['json'](path)
    } else {
      data = await Loaders['ftrn'](path)
    }
  
    cache[carCode] = data
    return data
  }
})()



/**
 *  
 * @param {String} folderName  
 * @param {String} name  
 */
const getAssetPath = function () {

  if (!State.carCode) {
    throw Error('carCode not found! (Function: getAssetPath)')
  }

  return `${carConfig[State.carCode].assets}/`
}




/**
 * 
 * @param {*} name    
 */
const getEnvMap = async function (name) {

  if (name === 'undefined') return 

  if (!State.carCode) {
    throw Error('carCode not found! (Function: getEnvMap)')
  }
 
  let usrData = await getUsrData(), assetPath = getAssetPath();

  if ( !usrData || !usrData.Cubes || !assetPath ) {
    throw Error(`usrData.Cubes || assetPath not found! (Function: getEnvMap)`)
  }


  const path = `${assetPath}cubes/${name}`

  // 
  if ( State.textureCache[path] ) {
    return State.textureCache[path]
  }
  
  let { PX, PY, PZ, NX, NY, NZ } = _findData(name, usrData.Cubes)

  if ( PX && PY && PZ && NX && NY && NZ ) {

    const cube = CubeLoader.setPath(`${path}/`).load([ PX, NX, PY, NY, PZ,  NZ ]);
    State.textureCache[path] = cube
    return cube
  }
}




/**
 * 
 * @param {*} name 
 */ 
const getTexture = async function (name) {
  
  if (name === 'undefined') return 
  if (!State.carCode) {
    throw Error('carCode not found! (Function: getEnvMap)')
  }

  let usrData = await getUsrData(State.carCode), assetPath = getAssetPath();

  if ( !usrData || !usrData.Textures || !assetPath ) {
    throw Error(`usrData.Textures || assetPath not found! (Function: getTexture)`)
  }

  const path = `${assetPath}textures/${name}`

  if ( State.textureCache[path] ) {
    return State.textureCache[path]
  }

  let { repeat }= _findData(name, usrData.Textures)

  if ( repeat && repeat.x && repeat.y ) {

    const texture = TextureLoader.load(path)
    texture.matrixAutoUpdate = false;

    
    texture.wrapS = texture.wrapT = THREE.RepeatWrapping;
    texture.repeat.x = repeat.x;
    texture.repeat.y = repeat.y;

    State.textureCache[path] = texture

    return texture
  } 
}


  // 
  function _findData(name, arr) {
    
    if ( Array.isArray(arr) && arr.length > 0 ) {
      for ( let item of arr) {
        if( item.name === name) {
          return item
        }
      }
    }  else {
      console.log(`param arr is warn(Function: _findData)`)
    }
  }



/**
 * tween
 * @param {*} start   
 * @param {*} to      
 * @param {number} time    
 * @param {Function} easing    ()
 * @param {Function} endFunc    ()
 */
const Ftween = (start, end, time, easing, endFunc=Function) => {
  return new Promise( resolve => {
    const tween = new TWEEN.Tween(start).to(end, time).start()
      .onComplete(async () => {
        await endFunc()
        resolve()
      })
    if (easing) tween.easing(easing)
  })
}


/**
 *  rgb
 * @param {*} color 
 */
const Hex2RGB = function (color) {
  Color.setHex(color) 
  return {
    r: Color.r,
    g: Color.g,
    b: Color.b
  }
}




export { getUsrData, getAssetPath, getEnvMap, getTexture, Ftween, Hex2RGB }


