
import { Scene, Matrix4, TextureLoader } from './Three.Constant.js'
import { getAssetPath, getUsrData } from './Tools' 
import Loaders from './Loaders'
import getMaterial from './materials/GetMaterial'
import process from './process/Process'
import  { AsciiArrayToStr, ArrayToint32} from './libs/compute'
import { addCarLight, loadFloor, addSpriteCarLight } from './LoadAssets.AfterModel'

const THREE = require('three')


const isType = val => Object.prototype.toString.call(val).slice(8, -1).toLowerCase();

let count = 0

const addModels = async ({ Models, Materials }) => {
  

  if (!Models || !Materials)  {
    throw Error('usrData Models || Materials is error(Function: addModels)')
  }
  
  let materials = {};

  for ( const item of Materials ) {
    materials[item.name] = await getMaterial(item)
  }

  const modelPath = `${getAssetPath()}models/`

  for ( const model of Models ) {

    let { type, path } = model, requestPath = `${modelPath}${path}`;

    let object = null

    if(type === 'FBX'){
      object = await Loaders[type](requestPath)
    }else{
      object = await Loaders[type](requestPath)
    }
    
    if (isType(object) === 'arraybuffer' ) {
      await addFtrm(object, materials, Materials)
    }
  }

  await loadFloor()

  await addSpriteCarLight()
}


async function adjustRotate (mesh) {
  let { Hotspot } = await getUsrData()
  if ( Hotspot && Hotspot.length > 0 ) {
    for (const hot of Hotspot) { 
      for ( const item of hot.models ) {
        if ( item.name === mesh.name ) {
          const [x, y, z] = item.pivotPoint
          
          mesh.geometry.applyMatrix(Matrix4.makeTranslation(x, y, z))
          mesh.position.x -= x;
          mesh.position.y -= y;
          mesh.position.z -= z;
        } 
      }
    }
  }
}



async function addFtrm (blob, materials, Materials) {
  let modelTotal = ArrayToint32(new Int8Array(blob.slice(68, 72)))
  let offset = 4 + 64 + 4;

  for ( let i = 0; i < modelTotal; i++ ) {
    //file_type
    offset += 1;
    //byte_offset
    let byte_offset = ArrayToint32(new Int8Array(blob.slice(offset, offset + 4)));
    offset += 4;
    //byte_length
    let byte_length = ArrayToint32(new Int8Array(blob.slice(offset, offset + 4)));
    offset += 4;
    //file_name
    let file_name_array = new Int8Array(blob.slice(offset, offset + 64));

    let name = AsciiArrayToStr(file_name_array);
    let drcContent = blob.slice(byte_offset, byte_offset + byte_length);
    offset += 64;

    let geometry = await Loaders['decoder'](drcContent)
    let mesh = new THREE.Mesh(geometry)
    mesh.name = name

    if (typeof mesh.material.materials !== 'undefined') {
      mesh.material.materials[0] = materials[name];
    } else {
      mesh.material = materials[name];
    }

    mesh.position.set(0, -80, 0)

    // console.log(mesh.name)
    Scene.add(mesh)
    await adjustRotate(mesh)
    
    count ++
    let num = Math.floor(count / (Materials.length) * 100)
    if ( num >= 0 && num <= 100) {
      process.processNum(num)
    }
  }
}

export default addModels


