/**
 *  entry 
 */

import State from './state/state'
import process from './three/process/Process'
import { getAssetPath, getUsrData } from './three/Tools'
import { Scene, Orbit } from './three/Three.Constant.js'
import pano from './pano/Init'
import fontregl from './three/InterFace'
import { toInterior, outInterior } from './three/Animation.Action'
import { addChargeModelFBX } from './three/LoadAssets.AfterModel'
import Loaders from './three/Loaders'
import Detector from './three/Detector'

window.fontre = new class {

  constructor() {
    this.debug = State.debug
    console.log(`svw verison: ${State.version}`)
  }

  supportWebgl() {
    return Detector.webgl
  }

  //  
  async showExterior (dom, carCode, exteriorColor, interiorColor) {

    // init 
    State.carCode = carCode
    State.isLoading = true

    process.processShow()

    await fontregl.showModel(dom, carCode)

    if ( State.isCancel ) {
      State.isCancel = false
      process.processNum(0) // 0
      fontregl.dispose()  // gl
      return 
    }
    
    State.isLoading = false
    State.loadedCar = carCode
    process.processHide()

    await this.changeExteriorColor(exteriorColor)
    await this.changeInteriorColor(interiorColor)

  }


  //  
  async showInterior(idName, carCode, exteriorColor, interiorColor) {

    State.carCode = carCode

    if ( State.loadedCar === carCode && State.sceneCache.length > 0 ) {
      Scene.children = State.sceneCache
    }
    
    fontregl.cancelAnimation() 
    await pano.init(...arguments)
  }

  
  async changeExteriorColor(colorCode) {
    await fontregl.changeCarShaderColor(colorCode) 
    await pano.changeExterColor(colorCode)
  }


  async changeInteriorColor(colorCode) {
    await fontregl.changeMaterials(colorCode) 
    await pano.changeInterColor(colorCode)
  }


  // 
  async modelSwitching (modelName) {
    if (!modelName) {
      throw Error(`${modelName} is not found!(Function: modelSwitching)`)
    }
    return await fontregl.modelSwitching(modelName)
  }


  // process
  setLoadingProgressCallback (show, hide, num) {
    process.processShowCallback = show
    process.processHideCallback = hide
    process.processNumCallback = num
  } 

  switchWheelAnimation (boole, isFast, isFuel, isComfort) {
    let speed = 1
    if(isFast) speed = 5  // 
    if(isFuel) speed = 3  // 
    if(isComfort) speed = 2 // 
    
    return fontregl.switchWheelAnimation(boole, speed)
  }
  
  animation () {
    return fontregl.animation()
  }

  cancelAnimation () {
    return fontregl.cancelAnimation()
  }

  resize( width, height) {
    console.log(width, height)
    return fontregl.resize(width, height)
  }


  openLights(bool) {
    return fontregl.openLights(bool)
  }

  // 
  async outToInside () {
    return await toInterior(true)
  }

  // 
  async insideToOut () {
    this.animation()
    return await outInterior()
  }

  // 
  async openLFDoor() {
    return await fontregl.openLFDoor()
  }

  
  dispose () {

    if ( State.isLoading ) {
      State.isCancel = true
    } else {
      State.sceneCache = Scene.clone().children
    }

    fontregl.dispose()  // gl
    pano.dispose()      //remove pano
  }

  resetCamera () {
    fontregl.resetCamera()
  }

  showSize() {
    return fontregl.showSize
  }

  changeView (name) {
    fontregl.changeView(name)
  }

  
  hideShade() {
    return fontregl.hideShade() // 
  }

  switchSellPoint (bool) {
    pano.hideHotSpot(bool)
    return fontregl.showSellPoint(bool)
  }
 
  toggleVR() {
    pano.toggleVR()
  }

}()


export default window.fontre


