// import State from '../../../state'
import { Scene } from '../../Three.Constant'
import { getUsrData, Ftween} from '../../Tools'

const SUN_ROOF = 'open_sunroof'  // 


// 
export const closeAllDoor = async () => {

  let { Hotspot } = await getUsrData()

  if (!Hotspot) {
    throw Error('Hotspot is ! ! ! ')
  }

  for ( let { name, models} of Hotspot ) {

    if ( name === SUN_ROOF ) break

    for ( let model of models ) {
        let mesh = Scene.getObjectByName(model.name)
        Ftween(mesh.rotation, { x: 0, y:0, z: 0 }, 400)
    }
  }
}




export const closeLFdoor = () => {

}


