const THREE = require('three')

const TWEEN = require('@tweenjs/tween.js'),
OUT = TWEEN.Easing.Circular.Out,
IN = TWEEN.Easing.Circular.In;


const cameraPath = [
  new THREE.Vector3(30, 50, 320),
  new THREE.Vector3(75, 40, 220),
  new THREE.Vector3(50, 20, 120),
  new THREE.Vector3(30, 10, 100)
] 

// 
const spriteAnimation = function (num) {
  let scale  = Math.abs(Math.sin( num / 25 ) * 16) + 2

  for (let value of Object.values(state.spriteState)) {
    if (value instanceof THREE.Sprite) {
      value.scale.set(scale, scale, scale)
    }
  }
}

// 
const toRotateWheel = async ( num, speed = 1 ) => {

  let { Hotspot, Floor } = await getUsrData()
  for ( let i of Hotspot ) {
    if ( i.name === 'rotate_wheel' ) {
      for ( let j of i.models) {
        let mesh = Scene.getObjectByName(j.name)
        if ( mesh ) {
          mesh.rotateZ( Math.PI / 60 * speed)
        }
      }
    }
  }

  // 
  if (Floor) {
    let floorMesh = Scene.getObjectByName(Floor.name)
    if(floorMesh) {
      floorMesh.material.map.offset.set(-num/150 * speed, 0)
    }
  }
}


