const doorAnimation = () => {
  const updateMove = async () => {
    await openDoor()
    await closeDoor()
    await openDoor()
    await closeDoor()
    state.autoAnimateObjs.doorAnimateTimer =
      setTimeout(updateMove, 20000)
  }
  updateMove()
}

