/**
 * 
 */
const closeDoor = async (isChange) => {
  let data = await getUsrData()
  let leftDoorData = data.Hotspot[0]

  let callBack

  let { models, hotspot } = leftDoorData

  if(isChange){ // 
    state.hotSpotState['lf_door'] = false
  }

  let tweenAll = []
  models.forEach( item => {
    let mesh = Scene.getObjectByName(item.name)
    if (!mesh) return 

    if ( state.spriteState[item.name] instanceof THREE.Sprite ) {
      state.spriteState[item.name].visible = !state.hotSpotState[name]
    }
    
    tweenAll.push(Ftween(mesh.rotation, {x: 0, y: 0, z: 0}, 300, IN, callBack))
  })
  await Promise.all(tweenAll)
}

/**
 * 
 * @param {*} Camera 
 */
const checkViewDirToAnimate = (Camera) => {
  let cameraDir = new THREE.Vector3()
  Camera.getWorldDirection(cameraDir)
  let carDir = new THREE.Vector3(1, 0, 0)
  let dotValue = carDir.dot(cameraDir.negate().normalize())
  if(dotValue > 0.95 || dotValue < -0.95){
    return true
  }
  return false
}

