import { bindEvent } from './Event'
import MouseMoveCallBack from './Event.MouseMove'
import { Camera, Renderer, Scene, Orbit } from './Three.Constant.js'
import State from '../state/state'
import Carconfig from '../config/carConfig'
import { createEl } from './shade'
import Postprocessing from './Postprocessing'

const init = function (container) {
	
	if (!State.carCode) {
    throw Error('carCode not found! (Function: Three.init)')
	}
	
	// container
	container.style.setProperty('width', '100%')
	container.style.setProperty('height', '100%')

	// camera
	try {

		if(/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)){ // 
			console.log('')
			let { x, y, z } = Carconfig[State.carCode].exteriorViewpoint.mobilePosition
			Camera.position.set(x, y , z)
		}else{
			console.log('PC')
			let { x, y, z } = Carconfig[State.carCode].exteriorViewpoint.position
			Camera.position.set(x, y , z)
		}

	} catch (err) {
		console.warn(err)
		Camera.position.set(745, 140, 653) // camera postion
	}
	Camera.lookAt(Scene.position) 
	Camera.fov = 45
	Camera.far = 100000
	Camera.aspect = container.clientWidth / container.clientHeight
	Camera.updateProjectionMatrix()

	// render
	Renderer.setSize(container.clientWidth, container.clientHeight)
	Renderer.setPixelRatio(window.devicePixelRatio)
	// Renderer.setClearColor(0x008000)
	// Renderer.autoClear = true  


	// orbit contriol
	// Orbit.enableKeys = false
	Orbit.autoRotate = true
	Orbit.autoRotateSpeed = -0.06
	
	Orbit.onClickCallback = bindEvent.bind(null, container)
	// Orbit.onMouseMoveCallBack = MouseMoveCallBack


	window.addEventListener('resize', ()=> {
		Camera.aspect = container.clientWidth / container.clientHeight
		Camera.updateProjectionMatrix()
		Renderer.setSize(container.clientWidth, container.clientHeight)

		// var pixelRatio = Renderer.getPixelRatio();
		// console.log(pixelRatio)
		// var newWidth  = Math.floor( container.clientWidth  / pixelRatio ) || 1;
		// var newHeight = Math.floor( container.clientHeight / pixelRatio ) || 1;
		// console.log('newWidth', newWidth)
		// console.log('newHeight', newHeight)
		// State.composer.setSize( container.clientWidth , container.clientHeight );
	})


	container.appendChild(Renderer.domElement)
	createEl(container)
}

export default init


