import { ValueType } from  './SetMaterial.Constant'
import { getTexture, getEnvMap } from '../Tools'


const setMaterial = async (object, parameter, children) => {

  for ( let i = 0, l = children.length; i < l; i++ ) {

    let { name, type } = children[i]

    if (!object.hasOwnProperty(name) || !parameter.hasOwnProperty(name)) {
      continue
    }

    switch (type) {

      case ValueType.Number:
        object[name] = parameter[name]
        break

      case ValueType.Bool: 
        object[name] = parameter[name]
        break

      case ValueType.Color:
        object[name].setHex(parameter[name])
        break


      case ValueType.Texture:
        object[name] = await getTexture(parameter[name])
        break

      case ValueType.EnvCub:
        object[name] = await getEnvMap(parameter[name])
        break

      case ValueType.Select:
        object[name] = parameter[name]
        break

      case ValueType.Vector2:
        _iterationObj(object[name], parameter[name])
        break
        
      case ValueType.Vector3:
        _iterationObj(object[name], parameter[name])
        break

      case ValueType.ColorRGB:
        _iterationObj(object[name], parameter[name])
        break

      default:
        throw Error('setMaterial not found type')
    }
  }
}

// materials
function _iterationObj(materObj, paramObj) {
  for (let name in paramObj) {
    materObj[name] = paramObj[name]
  }
}


export default setMaterial


