import { vertexShader, fragmentShader } from './CarPaintShader.Text'
import { getEnvMap, getTexture } from '../Tools'

const THREE = require('three')

const getCarPaintShader = async function(param) {
  return {
    fragmentShader: fragmentShader,
    vertexShader: vertexShader,
    uniforms: await Uniform(param),
    extensions: {
      derivatives: true
    }
  }
}


async function Uniform(materialData) {
    
  let { aoMap, envMap, color1, color2 } = materialData
  
  return {

    paintColor1: {
      type: "c",
      value: new THREE.Color(color1 || 0x000000)
    },
    paintColor2: {
      type: "c",
      value: new THREE.Color(color2 || 0x87878b)
    },
    map: {
      type: "t",
      value: await getTexture(aoMap) || null
    },
    normalMap: {
      type: "t",
      value: null
    },
    normalScale: {
      type: "f",
      value: 0.0,
      min: 0.0,
      max: 1.0
    },
    glossLevel: {
      type: "f",
      value: 2,
      min: 0.0,
      max: 5.0
    },
    envMap: {
      type: "t",
      value: await getEnvMap(envMap) || null
    },
    microflakeNMap: {
      type: "t",
      value: null
    },
    flakeColor: {
      type: "c",
      value: new THREE.Color(0xFF3388)
    },
    flakeScale: {
      type: "f",
      value: 10.0,
      min: -50.0,
      max: 1.0
    },
    normalPerturbation: {
      type: "f",
      value: 1.0,
      min: -1.0,
      max: 1.0
    },
    microflakePerturbationA: {
      type: "f",
      value: 0.05,
      min: -1.0,
      max: 1.0
    },
    microflakePerturbation: {
      type: "f",
      value: 0.2,
      min: 0.0,
      max: 1.0
    },
    filmic: {
      type: "b",
      value: true
    },
    time: {
      type: "f",
      value: 0.0
    }
  }
}

export default getCarPaintShader


