
import { Fileloader, FontreLoader, dracoLoader } from './Three.Constant.js'
import  { Utf8ArrayToStr } from './libs/compute'
import State from '../state/state'
const OBJLoader = require('three-obj-loader')
const THREE = require('three')
// require('./libs/inflate.min.js')
require('./libs/FBXLoader')
require('./libs/GLTFLoader')

const FBXLoader = new THREE.FBXLoader()
OBJLoader(THREE)

const Loaders = {

  'json': (() => {
    let caChe = {}
    return async path => {
      if ( caChe[path] ) return caChe[path]
      return new Promise( resolve => {
        Fileloader.load(`${path}.json`, data => {
          caChe[path] = JSON.parse(data)
          resolve(JSON.parse(data))
        })
      })
    }
  })(),

  'ftrn': (() => {
    let caChe = {}
    return async path => {
      if ( caChe[path] ) return  caChe[path] 
      let blob  = await FontreLoader.loadFtrn(`${path}.ftrn`)
      caChe[path] = JSON.parse(Utf8ArrayToStr(new Uint8Array(blob)))
      return caChe[path]
    }
  })(),


  'ftrm': path => {
    // cache
    if ( State.ftrmCache[path] ) {
      return State.ftrmCache[path] 
    }
    return new Promise( resolve => {
      let req = dracoLoader.loadFtrm( path, async blob => {
        State.ftrmCache[path] = blob
        resolve(blob)
      })
      State.requestList.push(req)
    })
  },


  'drc': async (path) => {
    return new Promise( res => {
      dracoLoader.load( path, function ( geometry ) {
        geometry.computeVertexNormals();
        let mesh = new THREE.Mesh(geometry);
        res(mesh)
      })
    })
  },


  decoder: (blob) => {
    return new Promise( resolve => {
      dracoLoader.decodeDracoFile(blob, geometry => {
        if (geometry.attributes.uv) {
          let uvs = geometry.attributes.uv.array 
          geometry.addAttribute("uv2", new THREE.BufferAttribute(uvs, 2));
        } else {
          console.log(`error: ${blob}`)
        }
        setTimeout(()=> { 
          resolve(geometry) 
        }, 0)
      })
    })
  },

  'obj': function(path) {

    return new Promise (res => {
      new THREE.OBJLoader().load( path, obj => {
        console.log(obj)
        const mesh = obj.children[0]
        res( mesh )
      })
    })

  },


  'FBX': function(path) {

    return new Promise (res => {  
      FBXLoader.load( path, obj => {

        // console.log(obj)
        res( obj )
      })
    })
  },

  'gltf': function(path) {
    return new Promise (res => {  
      new THREE.GLTFLoader().load( path, gltf => {
        res( gltf )
      })
    })
  }

 
}


export default Loaders


